/* Home Section */
.home {
    width: 100vw;
    height: 100vh;
    background: rgb(235, 235, 235);
    z-index: -1;
    position: absolute;
    display: flex;
    justify-content: center;
  }
  .home-greeting {
    display: flex;
    width: 100%;
    height: 27vh;
    background-color: #0D99FF;
    z-index: 0;
    position: absolute;
  }
  .home-greeting-message {
    position: relative;
    left: 140px;
  }

  .home-greeting-message p {
    position: relative;
    font-size: 35px;
    font-weight: 700;
    color: white;
    top: 40px;
    left: 150px;
  }
  .home-box {
    position: relative;
    top: 120px;
    height: 60vh;
    width: 60vw;
    background-color: white;
    z-index: 1;
    display: flex;
    flex-direction: column;
  }

  .home-box-top {
    position: relative;
    flex: 1;
    border-bottom: 1px solid black;
    display: flex;
  }
  .home-box-bottom {
    border-top: 1px solid black;
    flex: 1;
    display: flex;
  }
  .home-box-left {
    display: flex;
    flex: 1;
    flex-direction: column;
    padding: 10px 0px 0px 20px;
  }
  .home-box-right {
    display: flex;
    flex: 1;
    flex-direction: column;
    align-items: center;
    padding-top: 10px;
  }
  .home-box h1{
    padding-bottom: 20px;
  }
  .home-box-left-info-p{
    font-size: 28px;
    padding-bottom: 7px;
  }
  .home-box-left-view-p {
    position: absolute;
    font-size: 24px;
    bottom: 0;
    margin-bottom: 8px;
  }