/* Contact Page */
.contact {
  width: 100%;
  position: absolute;
  display: flex;
  flex-direction: column;
  align-items: center;
}

.contact-header {
  display:flex;
  justify-content: space-between;
  background-color: #0D99FF;
  width: 90vw;
  height: 12vh;
  display: flex;
  align-items: center;
  margin-top: 20px;
}
.contact-container {
  width: 90vw;
  min-height: 90vh;
  background-color: rgb(235, 235, 235);
  display: flex;
  margin-top: 0;
}
.contact-header-left {
  color: white;
  font-weight: 700;
  font-size: 40px;
  margin-left: 55px;
}
.contact-header img {
  height: 70px;
  width: 70px;
  transition: transform 0.3s ease;
}
.contact-header img:hover {
  cursor: pointer;
  transform: scale(1.4);
}
.contact-sidebar {
  display: flex;
  border-right: 1px solid rgb(146, 145, 145);
  flex-direction: column;
  min-width: 21vw;
  background-color: #D9D9D9;
}


.contact-content {
  display: flex;
}
