.contact-list-body {
  position: relative;
  max-height: 90vh;
  width: 100%;
  overflow-y: auto;
}
.contact-list-individual {
  width: 69vw;
  border-bottom: 1px solid rgb(159, 157, 157);
  display: flex;
  justify-content: space-between;
  align-items: center;
}
.contact-information p, .contact-information h3 {
  margin-left: 20px;
  margin: 20px;
}

.contact-remove-sign {
  margin-right: 20px;
  top: 10px;
  right: 10px;
  height: 20px;
  width: 20px;
  cursor: pointer;
}

.sidebar-phone, .sidebar-bank {
  font-size: 28px;
  border-bottom: 1px solid rgb(146, 145, 145);
  cursor: pointer;
}

.sidebar-phone:hover {
  background-color: rgb(244, 243, 243);
}

.sidebar-bank:hover {
  background-color: rgb(244, 243, 243);
}
.sidebar-phone-active, .sidebar-bank-active {
  font-size: 28px;
  border-bottom: 1px solid rgb(146, 145, 145);
  background-color: white;
  border-left: 10px solid #0D99FF;
  pointer-events: none;
}

.contact-sidebar p {
  margin-top: 25px;
  margin-left: 20px;
  margin-bottom: 25px;
}

.contact-content {
  display: flex;
  flex-direction: column;
}


/** Contact Form */
.contact-form-overlay {
  position: fixed;
  top: 0;
  left: 0;
  width: 100vw;
  height: 100vh;
  background: rgba(0, 0, 0, 0.5);
  display: flex;
  justify-content: center;
  align-items: center;
}

.contact-form-content {
  background: white;
  padding: 10px;
  border-radius: 10px;
  text-align: center;
  z-index: 5;
}
.contact-form-header {
  display: flex;
  align-items: center;
  justify-content: space-between;
  padding: 2px;
}
.contact-form-header p {
  flex: 1;
  text-align: center;
  margin: 0;
  font-weight: 700;
  font-size: 24px;
}
.contact-form-header img {
  height: 20px;
  width: 20px;
  cursor: pointer;
  transition: transform 0.3s ease;
}
.contact-form-header img:hover {
  transform: scale(1.6);
}
.contact-content img {
  height: 12px;
  width: 12px;
  cursor: pointer;
  transition: transform 0.3s ease;
}
.contact-content img:hover {
  transform: scale(1.6);
}
.contact-form-content span {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-left: 5px;
  margin-top: 15px;
}
.contact-form-label {
  font-weight: 500;
  font-size: 16px;
  flex: 1;
  margin-right: 20px;
}
.contact-form-fill-up {
  width: 250px;
  font-size: 16px;
  border-radius: 5px;
  border: 2px solid #ccc;
  background-color: white;
  color: #333;
  resize: none;
  margin-right: 10px;
}
.contact-form-content button {
  padding: 10px 20px;
  margin-top: 15px;
  font-size: 16px;
  background-color: #1a3b4a;
  color: white;
  border: none;
  border-radius: 35px;
  cursor: pointer;
}
.contact-form-content button:hover {
  background-color: #142d39
}

.remove-form-overlay {
  position: fixed;
  top: 0;
  left: 0;
  width: 100vw;
  height: 100vh;
  background: rgba(0, 0, 0, 0.5);
  display: flex;
  justify-content: center;
  align-items: center;
}

.remove-form-content {
  background: white;
  padding: 5px;
  border-radius: 10px;
  text-align: center;
  max-width: 400px;
  width: 90%;
  position: relative;
}

.remove-form-content img {
  position: absolute;
  top: 10px;
  right: 10px;
  height: 20px;
  width: 20px;
  cursor: pointer;
  transition: transform 0.3s ease;
}
.remove-form-content img:hover {
  transform: scale(1.6);
}
.remove-form-content p {
  margin-top: 30px;
  padding-left: 5px;
  padding-right: 5px;
  font-size: 16px;
}

.remove-form-content button {
  margin-top: 10px;
  margin-bottom: 10px;
  padding: 10px 20px;
  font-size: 16px;
  background-color: #1a3b4a;
  color: white;
  border: none;
  border-radius: 35px;
  cursor: pointer;
}
.remove-form-content button:hover {
  background-color: #142d39;
}
.highlight-name {
  font-weight: 700;
}
.hover-div {
  position: relative;
}

.hover-div::after {
  content: "Add Contact";
  position: absolute;
  top: 100%;
  left: 0;
  visibility: hidden;
  opacity: 0;
  transition: opacity 0.3s ease, visibility 0s 5s;
  color: white;
  font-weight: 500;
  font-size: 20px;
  background-color: #296298;
}

.hover-div:hover::after {
  visibility: visible;
  opacity: 1;
}
